.steps-list {
  display: grid;
  gap: 10px;

  @media (min-width: $width-tablet+px) {
    gap: 39px;
  }

  @media (min-width: $width-desktop+px) {
    gap: 44px;
  }
}

.step-item {
  background: #ffffff;
  border-radius: 10px;
  padding: 22px 25px 17px;

  @media (min-width: $width-tablet+px) {
    border-radius: 30px;
    padding: 48px 40px 56px 40px;
  }

  @media (min-width: $width-desktop+px) {
    border-radius: 60px;
    padding: 100px 160px 100px 160px;
    position: relative;
  }
}

.step-item__title {
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  color: #1d2e5b;
  margin-bottom: 17px;

  @media (min-width: $width-tablet+px) {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 34px;
  }
}

.step-item__text {
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: #444444;

  @media (min-width: $width-tablet+px) {
    font-size: 20px;
    line-height: 24px;
  }
}

.step-item__travel-set {
  display: grid;
  row-gap: 19px;
  grid-template-areas:
    "set-companions"
    "set-days"
    "set-child";

  @media (min-width: $width-tablet+px) {
    row-gap: 33px;
    grid-template-areas:
      "set-companions"
      "set-child"
      "set-days";
    margin-bottom: 59px;
  }

  @media (min-width: $width-desktop+px) {
    row-gap: 14px;
    column-gap: 60px;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "set-companions set-child"
      "set-days set-child";
    border-top: 1px solid #e1e3e9;
    padding-top: 30px;
    margin-bottom: 32px;
  }
}

.step-item__settings {
  display: grid;
  grid-template-columns: 1fr max-content;
  row-gap: 9px;

  @media (min-width: $width-tablet+px) {
    row-gap: 26px;
    border-top: 1px solid #e1e3e9;
    padding-top: 24px;
  }

  @media (min-width: $width-desktop+px) {
    grid-template-columns: 185px 180px 1fr;
    align-items: center;
    column-gap: 35px;

    border-top: 0;
    padding-top: 0;
  }

  &--companions {
    grid-area: set-companions;
  }

  &--days {
    grid-area: set-days;
  }
}

.step-item__setting-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  color: #1d2e5b;

  @media (min-width: $width-tablet+px) {
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 0.1em;
  }

  @media (min-width: $width-desktop+px) {
    letter-spacing: normal;
  }
}

.step-item__setting-subtext {
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: rgba($color: #1d2e5b, $alpha: 0.3);

  @media (min-width: $width-tablet+px) {
    font-size: 20px;
    line-height: 18px;
    letter-spacing: 0.08em;
    margin-right: 6px;
  }

  @media (min-width: $width-desktop+px) {
    color: #1d2e5b;
    order: 2;
    justify-self: start;
    margin-right: 0;
  }
}

.step-item__input-body {
  grid-column: 1/-1;

  @media (min-width: $width-desktop+px) {
    grid-column: initial;
  }
}

input[name="children-yes"]:checked ~ .step-item__child-cheks {
  &::after {
    content: "";
    background-image: url("../img/icon_check-mark.svg");
    background-position: center center;
    background-repeat: no-repeat;
  }

  @media (min-width: $width-tablet+px) {
    &::after {
      background-size: 18px 17px;
    }
  }
}

input[name="children-yes"] {
  &:focus,
  &:focus-within,
  &:hover {
    + .step-item__child-cheks {
      outline: inherit;

      &::after {
        border-color: #959bb2;
      }
    }
  }

  &:active {
    + .step-item__child-cheks {
      &::after {
        border-color: #e2e5f1;
        background-color: #e2e5f1;
        filter: opacity(0.3);
      }
    }
  }
}

.step-item__child-cheks {
  grid-area: set-child;
  font-weight: normal;
  font-size: 15px;
  line-height: 26px;
  color: #1d2e5b;
  position: relative;
  padding-left: 39px;
  margin-bottom: 22px;
  margin-top: 4px;
  max-width: max-content;

  @media (min-width: $width-tablet+px) {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 20px;
    line-height: 20px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    font-weight: 500;
    padding-left: 58px;
  }

  @media (min-width: $width-desktop+px) {
    margin: 25px 0 0;
    justify-self: end;
    align-self: start;
  }

  &::after {
    content: "";
    position: absolute;
    width: 26px;
    height: 26px;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    border: 1px solid #cbced9;
    border-radius: 4px;

    @media (min-width: $width-tablet+px) {
      width: 36px;
      height: 36px;
    }
  }
}

.step-item__calendar {
  margin-bottom: 21px;

  @media (min-width: $width-tablet+px) {
    margin-bottom: 48px;
  }

  @media (min-width: $width-desktop+px) {
    margin-bottom: 58px;
  }
}

.step-item__prev-step {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #1d2e5b;

  @media (min-width: $width-tablet+px) {
    justify-content: space-between;
    font-size: 24px;
    line-height: 22px;
    text-transform: none;
    justify-self: end;
    width: 185px;
  }

  @media (min-width: $width-desktop+px) {
    margin-left: 41px;
    justify-self: initial;
  }

  &::before {
    @media (min-width: $width-tablet+px) {
      content: "";
      vertical-align: middle;
      display: inline-block;
      border-top: 7px solid transparent;
      border-bottom: 7px solid transparent;
      border-right: 14px solid #1d2e5b;
    }
  }

  &:hover,
  &:focus,
  &:focus-within {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.step-item__btn-submit {
  width: 100%;

  @media (min-width: $width-tablet+px) {
    width: 286px;
  }
}

.step-item__plan-points {
  display: none;

  @media (min-width: $width-desktop+px) {
    display: flex;
    flex-direction: column;
  }
}

.step-item__anchor {
  scroll-margin-top: 100px;

  @media (min-width: $width-desktop+px) {
    scroll-margin-top: 0;
    position: absolute;
    top: 0;
  }
}
