.country-flag-list {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.country-flag-list__item {
  width: 35px;
  height: 25px;
  position: relative;

  @media (min-width: $width-tablet+px) {
    &:hover {
      & > .country-flag-list__item-tooltip {
        opacity: 1;
        visibility: visible;
        height: 72px;
      }
    }
  }
}

.country-flag-list__item-tooltip {
  transition: all 0.3s ease 0s;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 0;
  margin-top: -2px;
  padding-top: 15px;
}
