.input-body {
  width: 100%;
  display: grid;
  align-items: center;
  grid-template-columns: min-content 1fr min-content;
  border: 1px solid #cbced9;
  border-radius: 4px;
  padding: 11px;

  @media (min-width: $width-tablet+px) {
    padding: 14px 23px;
    height: 70px;
  }

  &:hover {
    border-color: #959bb2;

    .input-body__btn::after,
    .input-body__btn::before {
      background-color: #959bb2;
    }
  }

  &:active,
  &:focus,
  &:focus-within {
    border-color: #161c35;
    color: rgba($color: #000000, $alpha: 0.1);

    .input-body__btn::after,
    .input-body__btn::before {
      background-color: #161c35;
    }
  }
}

.input-body__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 14px;
  height: 14px;
  position: relative;
  background: transparent;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    height: 2px;
    background-color: #cccccc;
  }

  @media (min-width: $width-tablet+px) {
    width: 20px;
    height: 20px;
  }

  &:hover {
    &::after,
    &::before {
      background-color: #999999;
    }
  }

  &:active {
    &::after,
    &::before {
      background-color: #e5e5e5;
    }
  }
}

.input-body__btn--plus {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 100%;
    background: #cccccc;
  }
}

.input-body__input {
  width: 100%;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 14px;
  text-transform: uppercase;
  color: #1d2e5b;
  text-align: center;

  @media (min-width: $width-tablet+px) {
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: $width-desktop+px) {
    line-height: 18px;
  }
}
