.calendar {
  background: #ffffff;
}

.calendar__top {
  padding-top: 11px;
  padding-bottom: 11px;
  margin-bottom: 4px;
  border-top: 1px solid #d6d9e6;
  border-bottom: 1px solid #d6d9e6;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (min-width: $width-tablet+px) {
    padding: 25px 17px 24px;
    margin-bottom: 2px;
  }

  @media (min-width: $width-desktop+px) {
    padding: 26px 0 23px;
    margin: 0;
  }
}

.calendar__mth-btn {
  width: 9px;
  height: 18px;
  background-color: transparent;
  fill: #b3b3b3;

  @media (min-width: $width-tablet+px) {
    width: 19px;
    height: 34px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &:hover,
  &:focus,
  &:focus-within {
    fill: rgba($color: #000000, $alpha: 0.5);
  }

  &:active {
    fill: rgba($color: #000000, $alpha: 0.1);
  }
}

.calendar__month {
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  color: #1d2e5b;
  text-transform: capitalize;

  @media (min-width: $width-tablet+px) {
    font-size: 36px;
    line-height: 36px;
  }
}

.calendar__days-row {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  padding-top: 17px;
  padding-bottom: 10px;

  @media (min-width: $width-tablet+px) {
    padding-top: 35px;
    padding-bottom: 9px;
  }

  @media (min-width: $width-desktop+px) {
    padding-top: 16px;
    padding-bottom: 20px;
  }
}

.calendar__day {
  text-align: center;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: #444444;

  @media (min-width: $width-tablet+px) {
    font-size: 18px;
  }

  @media (min-width: $width-desktop+px) {
    text-align: left;
    padding-left: 15px;
  }
}

.calendar__day-second-letter {
  display: none;

  @media (min-width: $width-tablet+px) {
    display: inline;
  }
}

.calendar__day--weekend {
  color: #ff5c23;
}

.calendar__nums {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  align-content: center;
  justify-content: center;
  box-shadow: inset 0 0 0 0.5px #d6d9e6;
}

.calendar__number {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  color: rgba($color: #444444, $alpha: 0.3);
  height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: inset 0 0 0 0.5px #d6d9e6;

  @media (min-width: $width-tablet+px) {
    font-size: 18px;
    height: 69px;
  }

  @media (min-width: $width-desktop+px) {
    height: 71px;
    align-items: flex-start;
    justify-content: flex-start;
    padding-top: 14px;
    padding-left: 15px;
    flex-wrap: wrap;
  }
}

.calendar__number-month {
  display: none;

  @media (min-width: $width-desktop+px) {
    display: block;
    margin-left: 8px;
  }
}

.calendar__number-text {
  display: none;

  @media (min-width: $width-desktop+px) {
    display: block;
    flex: 1 0 100%;
  }
}

.calendar__number--current {
  border: 1.5px solid #ff5c23;
  outline: 0.5px solid #ff5c23;
  box-shadow: none;
  z-index: 5;

  @media (min-width: $width-tablet+px) {
    border-width: 3.5px;
  }

  @media (min-width: $width-desktop+px) {
    padding-top: 10px;
    padding-left: 11px;
  }
}

.calendar__number--choosen {
  background: #1d2e5b;
  color: #ffffff;
  font-weight: 500;
  position: relative;

  @media (min-width: $width-desktop+px) {
    font-weight: 400;

    &::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      box-shadow: 0 0 0 1px #ff5c23;
      z-index: 5;
    }

    &:hover:not(.calendar__number--current) {
      box-shadow: inset 0 0 0 3px #ff5c23;

      &::after {
        content: "";
      }
    }
  }
}

.calendar__number--future {
  background: #edeff6;
  box-shadow: inset 0 0 0 0.5px #cbced9;
  color: #1d2e5b;
  font-weight: 500;

  @media (min-width: $width-desktop+px) {
    font-weight: 400;
  }
}
