.filter {
  background: #edeff6;
  position: relative;

  @include sectionRadiusBackground;
}

.filter__container {
  background: #edeff6;
  margin-bottom: 20px;

  @media (min-width: $width-tablet+px) {
    margin-bottom: 43px;
  }

  @media (min-width: $width-desktop+px) {
    max-width: 100%;
  }
}

.filter__content {
  padding-bottom: 76px;

  @media (min-width: $width-tablet+px) {
    padding-bottom: 122px;
  }

  @media (min-width: $width-desktop+px) {
    padding-bottom: 137px;
    display: grid;
    grid-template-columns: 1fr 285px;
    column-gap: 85px;
    grid-template-areas:
      "companions settings"
      "show-more settings"
      "pagination settings";
  }
}

.filter__country {
  margin: 0 -25px;
  padding: 0 25px;
  position: relative;

  @include sectionRadiusBackground;

  @media (min-width: $width-tablet+px) {
    margin: 0 -40px;
  }

  @media (min-width: $width-desktop+px) {
    margin: 0 0 60px;
  }
}

.filter__sets {
  grid-area: settings;
  align-self: start;
  margin-bottom: 28px;

  @media (min-width: $width-tablet+px) {
    margin-bottom: 40px;
  }

  @media (min-width: $width-desktop+px) {
    margin-top: 0;
  }
}

.filter__btn-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 41px;
  background: #ffffff;
  border-radius: 25px;
  font-weight: bold;
  font-size: 17px;
  line-height: 17px;
  text-transform: uppercase;
  color: #161c35;

  @media (min-width: $width-tablet+px) {
    min-height: 50px;
    font-size: 20px;
    line-height: 20px;

    &:hover,
    &:focus-within,
    &:focus {
      filter: drop-shadow(0 3px 6px rgba(0, 0, 0, 0.14));
    }
  }

  &:active {
    opacity: 0.3;
  }
}

.filter__companions {
  grid-area: companions;
  margin-bottom: 13px;

  @media (min-width: $width-tablet+px) {
    margin-bottom: 34px;
  }

  @media (min-width: $width-desktop+px) {
    margin-bottom: 50px;
  }
}

.filter__companions-show-more {
  grid-area: show-more;
  display: flex;
  align-items: center;
  justify-content: center;
  width: max-content;
  padding-left: 28px;
  margin: 0 auto 16px;
  color: #1d2e5b;
  text-transform: uppercase;
  background: transparent;
  font-weight: bold;
  font-size: 17px;
  line-height: 30px;
  min-height: 40px;
  position: relative;

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -4px;
    width: 18px;
    height: 2px;
    background: #1d2e5b;
    border-radius: 2px;
  }

  &::after {
    transform: rotate(90deg);
  }

  @media (min-width: $width-tablet+px) {
    margin-bottom: 39px;
    font-size: 20px;
    line-height: 20px;
    min-height: 50px;
    padding-left: 44px;

    &::after,
    &::before {
      width: 26px;
      left: -7px;
    }

    &:hover {
      &::after,
      &::before {
        background: rgba($color: #1d2e5b, $alpha: 0.6);
      }
    }
  }

  @media (min-width: $width-desktop+px) {
    padding-left: 24px;
    padding-right: 50px;
    margin-bottom: 58px;

    &::after,
    &::before {
      left: -24px;
    }
  }

  &:active {
    &::after,
    &::before {
      background: rgba($color: #1d2e5b, $alpha: 0.3);
    }
  }
}

.filter__pagination {
  grid-area: pagination;
}

.filter--no-js {
  .filter-country__list-wrapper {
    display: block;
    position: relative;
  }

  .filter-country__btn {
    display: none;
  }

  .filter-country__btn-close-countrylist {
    display: none;
  }
}
