.btn-filter-country {
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  outline: none;

  @media (min-width: $width-tablet+px) {
    justify-content: space-between;
    font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: #192144;
    min-width: 185px;

    &[aria-expanded="true"] {
      justify-self: end;

      .btn-filter-country__btn-close {
        display: block;
        opacity: 1;
        visibility: visible;
        position: relative;
        color: inherit;
        font-size: inherit;
        padding-left: 11px;
      }
    }

    &[aria-expanded="false"] {
      .btn-filter-country__btn-show {
        display: block;
        opacity: 1;
        visibility: visible;
        position: relative;
        color: inherit;
        font-size: inherit;
      }
    }

    &:hover {
      color: rgba($color: #192144, $alpha: 0.6);

      .btn-filter-country__icon {
        background: rgba($color: #192144, $alpha: 0.6);

        &::after,
        &::before {
          background: rgba($color: #192144, $alpha: 0.6);
        }
      }
    }
  }

  &:focus-within,
  &:focus {
    opacity: 0.6;
    outline: auto;
  }

  &:active {
    opacity: 0.3;
  }
}

.btn-filter-country__btn-show {
  transition: all 0.5s ease 0s;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  color: transparent;
  font-size: 0;
}

.btn-filter-country__btn-close {
  transition: all 0.5s ease 0s;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  color: transparent;
  font-size: 0;
}

.btn-filter-country__icon {
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background: #192144;
  position: relative;

  &::after,
  &::before {
    content: "";
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background: #192144;
    position: absolute;
    top: 0;
  }

  &::after {
    left: 7px;
  }

  &::before {
    left: 14px;
  }

  @media (min-width: $width-tablet+px) {
    width: 6px;
    height: 6px;
    margin-right: 24px;

    &::after,
    &::before {
      width: 6px;
      height: 6px;
    }

    &::after {
      left: 12px;
    }

    &::before {
      left: 24px;
    }
  }
}

.btn-filter-country[aria-expanded="false"] .btn-filter-country__icon {
  display: block;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  position: relative;
  transition: all 0.5s ease 0s;
  margin: 5px 15px 5px auto;

  &::after,
  &::before {
    transition: all 0.5s ease 0s;
    content: "";
    width: 3px;
    height: 3px;
    border-radius: 50%;
    position: absolute;
    top: 0;
  }

  &::after {
    left: 7px;
  }

  &::before {
    left: 14px;
  }

  @media (min-width: $width-tablet+px) {
    width: 6px;
    height: 6px;
    margin: initial;
    margin-right: 24px;

    &::after,
    &::before {
      width: 6px;
      height: 6px;
    }

    &::after {
      left: 12px;
    }

    &::before {
      left: 24px;
    }
  }
}

.btn-filter-country[aria-expanded="true"] .btn-filter-country__icon {
  width: 16px;
  height: 16px;
  border-radius: 0;
  background: transparent;
  margin: 0;
  margin-left: auto;

  @media (min-width: $width-desktop+px) {
    width: 22px;
    height: 24px;
  }

  &::after,
  &::before {
    content: "";
    width: 20px;
    height: 2px;
    border-radius: 0;
    left: 50%;
    top: 50%;
    right: initial;
    transition: all 0.5s ease 0s;

    @media (min-width: $width-tablet+px) {
      width: 22px;
    }
  }

  &::after {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::before {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}
